* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  font-family: "Quicksand", sans-serif;
}

body .wrapper {
  height: 100vh;
  background-color: #3f3f3f;
}

body .wrapper .header_container {
  position: fixed;
  width: 100%;
  z-index: 2;
  height: 0px;
}

body .wrapper .header_container header {
  background-color: #3f3f3f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 0 20px 0;
}

body .wrapper .header_container header #title_container {
  display: flex;
  align-items: center;
  cursor: pointer;
}

body .wrapper .header_container header #title_container #icon_title {
  width: 23px;
}

body .wrapper .header_container header #title_container h1 {
  font-family: "Special Elite", cursive;
  font-size: 29px;
  padding-top: 8px;
  padding-bottom: 15px;
  margin: 0px;
  color: white;
}

body .wrapper .header_container header .search_container {
  display: flex;
  align-items: center;
}

body .wrapper .header_container header .search_container input {
  border-radius: 2rem;
  border: none;
  height: 30px;
  width: 200px;
  margin-right: 5px;
  padding: 10px;
  outline: none;
  cursor: text;
}

body .wrapper .header_container header .search_container button {
  border-radius: 2rem;
  border: none;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-bottom: none;
  color: #259179;
  cursor: pointer;
  outline: none;
}

body .wrapper .header_container header .search_container button .icon_search {
  font-size: 15px;
}

body .wrapper .header_container .section_container {
  background-color: rgba(37, 145, 122, 0.911);
  min-height: 100vh;
  width: 30%;
  border-top: 7px solid white;
  border-right: 7px solid white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

body .wrapper .header_container .section_container .icon_fav {
  font-size: 20px;
  color: white;
  cursor: pointer;
  padding: 10px;
}

body .wrapper .header_container .section_container .icon_fav:hover {
  animation: beat .40s infinite alternate;
}

body .wrapper .header_container .section_container #favourites_list {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: 100%;
}

body .wrapper .header_container .section_container #favourites_list .listSeries {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background: white;
  border-radius: 20px;
  width: 80%;
  margin-bottom: 9px;
  padding: 4px;
}

body .wrapper .header_container .section_container #favourites_list .listSeries img {
  width: 35px;
  border-radius: 20px;
}

body .wrapper .header_container .section_container #favourites_list .listSeries .main-title {
  font-size: 12px;
  text-align: center;
  padding: 8px;
}

body .wrapper .header_container .section_container #favourites_list .listSeries .deleteButton {
  border-radius: 50%;
  font-size: 14px;
  font-weight: 200;
  height: 18px;
  width: 18px;
  background-color: #259179;
  color: white;
  padding-bottom: 6px;
  border: none;
  cursor: pointer;
}

body .wrapper .main_container {
  background-color: #3f3f3f;
  display: flex;
  flex-direction: column;
  justify-content: end;
}

body .wrapper .main_container .gif_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  padding-right: 20px;
}

body .wrapper .main_container .gif_container #gif_tv {
  width: 240px;
  border: solid 12px white;
  border-radius: 2rem;
  margin-top: 200px;
}

body .wrapper .main_container ul {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 110px 0px 0px 0px;
}

body .wrapper .main_container ul .alert {
  text-align: center;
  color: white;
  font-size: 24px;
}

body .wrapper .main_container ul .listSeries {
  width: 236px;
  height: 366px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  border-radius: 32px;
  box-shadow: 8px 8px 18px 0px rgba(0, 0, 0, 0.75);
  cursor: pointer;
  z-index: 1;
}

body .wrapper .main_container ul .listSeries:hover {
  transform: scale(1.05);
}

body .wrapper .main_container ul .listSeries img {
  width: 88%;
  border-radius: 21px;
  margin-bottom: 12px;
}

body .wrapper .main_container ul .listSeries .main-title {
  font-size: 15px;
  font-weight: 600;
  text-align: center;
}

body .wrapper .main_container ul .favouritesMainStyle {
  background-color: #259179;
  color: white;
}

body .wrapper .main_container ul .icon_fav {
  margin-top: 3px;
  font-size: 10px;
  color: white;
}

@media all and (min-width: 768px) {
  body .wrapper .header_container header {
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px 0 40px;
  }

  body .wrapper .header_container header #title_container #icon_title {
    width: 33px;
  }

  body .wrapper .header_container header #title_container h1 {
    font-size: 42px;
    padding-top: 8px;
    padding-bottom: 0px;
  }

  body .wrapper .header_container header .search_container input {
    height: 45px;
    width: 330px;
    margin-right: 7px;
    padding: 10px;
  }

  body .wrapper .header_container header .search_container button {
    width: 45px;
    height: 45px;
  }

  body .wrapper .header_container header .search_container button .icon_search {
    font-size: 22px;
  }

  body .wrapper .header_container .section_container {
    width: 100%;
    min-height: 90px;
    min-height: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 10px 0 18px;
    border-top: 7px solid white;
    border-right: none;
  }

  body .wrapper .header_container .section_container #favourites_list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  body .wrapper .header_container .section_container #favourites_list .listSeries {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 250px;
    margin: 10px;
    padding: 0;
    width: auto;
  }

  body .wrapper .header_container .section_container #favourites_list .listSeries img {
    width: 39px;
    border-radius: 46px;
  }

  body .wrapper .header_container .section_container #favourites_list .listSeries .main-title {
    font-size: 15px;
    padding: 8px;
  }

  body .wrapper .header_container .section_container #favourites_list .listSeries .deleteButton {
    margin-right: 8px;
  }

  body .wrapper .main_container .gif_container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-right: 0px;
  }

  body .wrapper .main_container .gif_container #gif_tv {
    width: 300px;
  }

  body .wrapper .main_container ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 170px 30px 0 30px;
  }
}

@keyframes beat {
  to {
    transform: scale(1.2);
  }
}
